/* Container & grid */
.hero.container-full {
  max-width: 2400px;
  margin: 0 auto;
  padding: 100px;
}

.hero-grid {
  display: grid;
  grid-template-columns: 1.1fr 1fr; /* about | expertise */
  gap: clamp(18px, 2.5vw, 28px);
  align-items: start;
}

/* Card */
.card.glass {
  border-radius: 18px;
  padding: 28px;
  border: 1px solid rgba(255,255,255,0.12);
  background: radial-gradient(1200px 700px at 50% -20%, rgba(255,255,255,0.06), rgba(255,255,255,0.02)) , rgba(10, 14, 25, .45);
  box-shadow: 0 6px 26px rgba(0,0,0,0.35);
}

/* About header */
.about-card .avatar-wrap{
  display: flex;
  justify-content: flex-start;
  margin-bottom: 10px;
}

.avatar {
  width: clamp(72px, 9vw, 112px);
  height: clamp(82px, 10vw, 118px);
  border-radius: 50%;
  object-fit: cover;
  border: 2px solid rgba(255,255,255,0.15);
  box-shadow: 0 6px 26px rgba(0,0,0,0.35);
}

.avatar--fallback {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, #253047, #111827);
  color: #e8eefb;
  font-weight: 700;
  font-size: clamp(20px, 3vw, 32px);
}

/* Text */
.about-title {
  font-size: clamp(28px, 3.4vw, 40px);
  line-height: 1.15;
  margin: 6px 0 2px;
  color: #f2f6ff;
}

.tagline {
  color: #dce1e9;
  margin-bottom: 12px;
  font-size: clamp(1.4rem, 1.2vw, 1.3rem);
}

/* Links row */
.about-links {
  display: flex;
  flex-wrap: wrap;
  gap: 8px 10px;
  margin-bottom: 26px;

  .icon-link {
    display: inline-flex;
    align-items: center;
    gap: 10px;
    padding: 6px 10px;
    border-radius: 999px;
    font-size: 0.95rem;
    background: rgba(255,255,255,0.06);
    border: 1px solid rgba(255,255,255,0.10);
    color: #e9eef7;
  }
}

/* Bulleted summary */
.about-body {
  p { margin: 0 0 10px; color: #e7eaf3; }
  .bullets {
    list-style: disc;
    padding-left: 18px;
    li { margin: 4px 0; font-size: clamp(1.2rem, 1.2vw, 1.1rem); line-height: 1.6;}
  }
}

/* Expertise area when embedded */
.expertise-aside {
  min-width: 0; /* prevent overflow */
}

/* Responsive */
@media (max-width: 1124px) {
  .hero-grid { grid-template-columns: 1fr; }
}

/* ---- tiny safeguard so grid children never cause horizontal scroll ---- */
.hero-grid > * { min-width: 0; }
